import React from 'react'
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import react_logo from '../assets/img/react_logo.png'
import html_logo from '../assets/img/html-5-logo-svgrepo-com.svg'
import css_logo from '../assets/img/css-svgrepo-com.svg'
import js_logo from '../assets/img/js-svgrepo-com.svg'
import tailwind_logo from '../assets/img/tailwindcss-icon-svgrepo-com.svg'
import bootstrap_logo from '../assets/img/bootstrap-5-1.svg'
import redux_logo from '../assets/img/redux-logo-svgrepo-com.svg'
import mui_logo from '../assets/img/material-ui-svgrepo-com.svg'
import mysql_logo from '../assets/img/mysql-logo.svg'
import python_logo from '../assets/img/python-5.svg'

function Skills() {
    const responsive = {
        superLargeDesktop: {
          // the naming can be any, depends on you.
          breakpoint: { max: 4000, min: 3000 },
          items: 5
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };

  return (
    <section className="skill" id="skills">
    <div className="container">
        <div className="row">
            <div className="col-12">
                <div className="skill-bx wow zoomIn">
                    <h2>Skills</h2>
                    <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.<br></br> Lorem Ipsum has been the industry's standard dummy text.</p>
                    <Carousel responsive={responsive} infinite={true} className="owl-carousel owl-theme skill-slider">
                        <div className="item">
                            <img src={html_logo} alt="Image" />
                            <h5>Html5</h5>
                        </div>

                        <div className="item">
                            <img src={css_logo} alt="Image" />
                            <h5>Css3</h5>
                        </div>

                        <div className="item">
                            <img src={react_logo} alt="Image" />
                            <h5>React JS</h5>
                        </div>

                        <div className="item">
                            <img src={js_logo} alt="Image" />
                            <h5>Java Script</h5>
                        </div>

                        <div className="item">
                            <img src={bootstrap_logo} alt="Image" />
                            <h5>Bootstrap</h5>
                        </div>

                        <div className="item">
                            <img src={redux_logo} alt="Image" />
                            <h5>Redux Toolkit</h5>
                        </div>

                        <div className="item">
                            <img src={mui_logo} alt="Image" />
                            <h5>Material UI</h5>
                        </div>

                        <div className="item">
                            <img src={tailwind_logo} alt="Image" />
                            <h5>Tailwind Css</h5>
                        </div>

                        <div className="item">
                            <img src={python_logo} alt="Image" />
                            <h5>Python</h5>
                        </div>

                        <div className="item">
                            <img src={mysql_logo} alt="Image" />
                            <h5>MySQL</h5>
                        </div>

                    </Carousel>
                </div>
            </div>
        </div>
    </div>
    </section>
  );
}

export default Skills;