import React from 'react'
import { useState,useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import { ArrowRightCircle } from 'react-bootstrap-icons'
import devImg from "../assets/img/DEV.svg";
import aiImg from "../assets/img/AI.svg";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
    const toRotate = [ "an AI Engineer", "a Front-end developer" ];
    const [loopNum, setLoopNum] = useState(0);
    const [index, setIndex] = useState(1);
    const [isDeleting, setIsDeleting] = useState(false);
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const period = 2000;
    
    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta)
        return () => {clearInterval(ticker)}
    },[text])

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);
    
        setText(updatedText);
    
        if (isDeleting) {
          setDelta(prevDelta => prevDelta / 2);
        }
    
        if (!isDeleting && updatedText === fullText) {
          setIsDeleting(true);
          setIndex(prevIndex => prevIndex - 1);
          setDelta(period);
        } else if (isDeleting && updatedText === '') {
          setIsDeleting(false);
          setLoopNum(loopNum + 1);
          setIndex(1);
          setDelta(500);
        } else {
          setIndex(prevIndex => prevIndex + 1);
        }
      }

  return (
    <section className='banner' id='home'>
        <Container>
            <Row className='align-items-center'>
                <Col xs={12} md={6} xl={12}>
                  <TrackVisibility>
                  {({ isVisible }) =>
                    <div className={isVisible ? "animate__animated animate__fadeInDown" : ""}>
                      <span className='tagline'>Welcome to my portofolio</span>
                      <h1>{`Hi I'm John, and I'm `}<span className='wrap'>{text}</span></h1>
                      <p >I'm very skilled with a unique view to web development</p>
                    </div>}
                  </TrackVisibility>           
                </Col>
                </Row>
                <Row>
                <Col xs={12} md={6} xl={6}>
                    <img src={devImg} alt='developer tags' />
                </Col>
                <Col xs={12} md={6} xl={6}>
                    <img src={aiImg} alt='AI' className='ai'/>
                </Col>  
            </Row>
        </Container>

    </section>
  )
}
